@use  '../base/mixins' as m;
@use '../base/variables' as v;

.reestablecer{
    margin-top: 5rem;
    .uptask{
        @include m.textoDegradado(v.$azul);
    }
    input[type="submit"]{
        @include m.boton(v.$azul, v.$blanco);
        border-radius: 1.5rem;
    }

    .acciones{
        a{
            color: v.$azul;
        }
    }
}