@use  '../base/mixins' as m;
@use '../base/variables' as v;

.dashboard{
    background-color: v.$grisClaro;

    @include m.tablet {
        display: flex;
        height: 100vh;
    }

    .principal{
        flex: 1;
        @include m.tablet {
            overflow-y: scroll;
        }
    }

    .formulario{
        input:not([type="submit"]){
            background-color: darken(v.$blanco, 15%);
        }

        input[type="submit"]{
            @include m.boton(v.$indigo,v.$blanco);
            border-radius: 1.5rem;
        }
    }

    .no-proyecto{
        text-align: center;
    }

    .listado-proyectos{
        list-style: none;
        padding: 0;
        margin-top: 5rem;

        @include m.grid(1,2rem);

        @include m.tablet {
            @include m.grid(2,2rem);
        }
        @include m.desktop {
            @include m.grid(3,2rem);
        }
        .proyecto{
            background-color: lighten(v.$indigo, 5%);
            min-height: 12rem;
            display: flex;
            align-items: center;
            padding: 3rem;
            line-height: 2;
            border-radius: 1.5rem;
            text-align: center;

            &:hover{
                background-color: darken(v.$indigo, 5%);
            }
            a{
                color: v.$blanco;
            }
        }
    }
    .enlace{
        color: v.$indigo;
    
        &:hover{
            color: darken(v.$indigo, 20%);
            border-bottom: 1px solid black;
        }
    }

}

.contenido{
    padding: 2rem;
    .nombre-pagina{
        font-size: 2.8rem;
        text-align: center;
    }
}

