@use  '../base/mixins' as m;
@use '../base/variables' as v;

.crear{
    margin-top: 5rem;
    .uptask{
        @include m.textoDegradado(v.$morado);
    }
    input[type="submit"]{
        @include m.boton(v.$morado, v.$blanco);
        border-radius: 1.5rem;
    }

    .acciones{
        a{
            color: v.$morado;
        }
    }
}