
@use  '../base/mixins' as m;
@use '../base/variables' as v;

@media (prefers-color-scheme: dark){
    $grisOscuro: #2c2c3b;
    body{
        background-color: $grisOscuro;
    }

    .tagline, .formulario label,
    .login .acciones a,
    .crear .acciones a,
    .olvide .acciones a,
    .contenido .nombre-pagina,
    .modal .formulario legend,
    .listado-tareas .no-tareas{
        color: v.$blanco;
    }
    .descripcion-pagina,
    .formulario .campo input
    {
        color: v.$grisClaro;
    }

    .dashboard .enlace{
        color: v.$grisClaro;

        &:hover{
            color: darken(v.$grisClaro, 20%);
            border-bottom-color: white;
        }
    }

    .dashboard .formulario input:not([type=submit]){
        background-color: lighten($grisOscuro, 15%);
        color: #f3f4f6;
    }

    .formulario .campo input{
        background-color: lighten($grisOscuro, 15%);
        color: #f3f4f6;
    }

    .barra-mobile, .barra{
        background-color: darken($grisOscuro, 7%);
    }

    .barra p{
        color: v.$grisClaro;

        span{
            color: v.$naranja;
        }
    }
    @include m.tablet {
        .sidebar{
            background-color: darken($grisOscuro, 5%);
        }
    }
    .filtros{
        background-color: lighten($grisOscuro, 10%);
        h2,label{
            color: v.$blanco;
        }
        .filtros-inputs .campo input[type=radio]{
            accent-color: v.$naranja;
        }
    }
    .dashboard{
        background-color: $grisOscuro;
    }

    .dashboard .listado-proyectos .proyecto{
        background-color: lighten($grisOscuro, 20%);
        &:hover{
            background-color: lighten($grisOscuro, 15%); 
        }
    }
    .listado-tareas .tarea{
        p{
            color: v.$grisClaro;
        }
    }

    .modal .formulario{
        background-color: $grisOscuro;
    }
}