@use  '../base/mixins' as m;
@use '../base/variables' as v;


.filtros{
    margin-top: 5rem;
    background-color: darken(v.$grisClaro, 10%);
    padding: 2rem;
    border-radius: 1.5rem;
    .filtros-inputs{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        @include m.tablet {
            flex-direction: row;
            gap: 3rem;
        }

        h2{
            font-size: 2.4rem;
            @include m.tablet {
                margin: 0;
            }
            text-transform: uppercase;
        }

        .campo{
            margin-bottom: 2rem;
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            gap: 1rem;
            @include m.tablet {
                margin-bottom: 0;
            }

            input[type="radio"]{
                accent-color: v.$indigo;
                width: 1.5rem;
                height: 1.5rem;
            }
        }
    }
}