@use  '../base/mixins' as m;
@use '../base/variables' as v;

.sidebar{
    opacity: 0;
    height: 0;
    background-color: v.$indigo;
    transition-property: opacity;
    transition-duration: 500ms;
    overflow: hidden;
    &.visible{
        opacity: 1;
        padding: 2rem;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgb(0 0 0 / .90);

        @include m.tablet {
            display: none;
        }
    }
    &.ocultar{
        opacity: 0;
    }
    @include m.tablet {
        opacity: 1;
        height: 100%;
        padding: 2rem;
        background-color: v.$indigo;
    }
    
    .contenedor-sidebar{
        display: flex;
        justify-content: space-between;
        align-items: center;
        h2{
            color: v.$blanco;
            margin: 0;
            text-align: center;
        }

        .cerrar-menu{
            display: flex;
            justify-content: center;
            height: 3rem;
            width: 3rem;

            &:hover{
                cursor: pointer;
            }

            @include m.tablet {
                display: none;
            }
        }
    }

    
    .sidebar-nav{
        margin-top: 5rem;
        
        a{
            color: v.$blanco;
            display: block;
            margin-bottom: 2.5rem;
            padding: 1rem;
            transition: all 300ms linear;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 2.5rem;
            text-align: center;
            &:hover{
                background-color: darken(v.$indigo, 25%);
                border-radius: 1rem;
            }
        }
        .activo{
            background-color: darken(v.$indigo, 25%);
            border-radius: 1rem;
        }

        @include m.tablet {
            a{
                font-size: 1.5rem;
                margin-bottom: 1.5rem;
                text-align: left;
            }
        }
    }

    .cerrar-sesion-mobile{
        margin-top: 10rem;
        @include m.tablet {
            display: none;
        }
        .cerrar-sesion{
            @include m.boton(v.$cyan, v.$blanco);
            font-size: 1.5rem;
            padding: 1.5rem;
            border-radius: 1rem;
        }
    }
}

