@use  '../base/mixins' as m;
@use '../base/variables' as v;

.contenedor-nueva-tarea{

    margin-top: 5rem;
    display: flex;
    gap: 2rem;
    justify-content: center;
    .agregar-tarea,.eliminar-proyecto{
        @include m.boton(v.$cyan,v.$blanco);
        border-radius: .5rem;
    }


    
}
.listado-tareas{
    list-style: none;
    padding: 0;
    margin-top: 3rem;

    .no-tareas{
        text-align: center;
    }
}

.tarea{
    padding: .5rem 2rem;
    border-bottom: 2px solid darken(v.$grisClaro, 5%);
    margin-bottom: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:last-of-type{
        border: none;
    }

    p{
        font-size: 1.4rem;
    }

    .opciones{
        display: flex;
        gap: 1.5rem;
        button{
            border: none;
            font-size: 1rem;
            padding: .5rem 1rem;
            text-transform: uppercase;
            font-weight: 900;
            border-radius: 1rem;

            &:hover{
                cursor: pointer;
            }
        }

        .completa{
            background-color: v.$azul;
            color: v.$blanco;
        }
        .pendiente{
            background-color: v.$naranja;
            color: v.$blanco;
        }

        .eliminar-tarea{
            background-color: lighten(v.$rojo, 15%);
            color: v.$blanco;
        }
    }

}